@import url('https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap');
:root{
  --sidebar-flex:1.5;
  --condiv-flex:6;
  --fontsize-p:1.2rem;
  --fontsize-nav:1.6rem;
  --typing-font:3rem;
}
*
{
  margin:0;
  padding: 0;
}
body{
  font-family: 'Raleway';
}
.App
{
  display: -ms-flexbox;
  display: flex;
  width: 100vw;
  min-height: 100vh;
}
nav
{
  padding:60px 30px 0px 30px ;
  -ms-flex:var(--sidebar-flex) 1;
      flex:var(--sidebar-flex) 1;
  background: black; 
  background: -webkit-gradient(linear, left top, left bottom, color-stop(50%, rgb(0, 0, 0)), color-stop(80%, rgb(20, 33, 61))); 
  background: -webkit-linear-gradient(top, rgb(0, 0, 0) 50%, rgb(20, 33, 61) 80%); 
  background: -o-linear-gradient(top, rgb(0, 0, 0) 50%, rgb(20, 33, 61) 80%); 
  background: linear-gradient(180deg, rgb(0, 0, 0) 50%, rgb(20, 33, 61) 80%);
}
.condiv
{
  padding: 100px ;
  background-color: #e5e5e5; 
  -ms-flex:var(--condiv-flex) 1; 
      flex:var(--condiv-flex) 1;
}

.home
{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  
}
.profilepic
{
  border-radius: 50%;
  width: 400px;
  margin: 30px;
}
.typingeffect
{
  font-size: var(--typing-font);
  margin-bottom: 30px;
  color: #14213d; 
}

nav ul{
  font-size:var(--fontsize-nav) ;
  text-align: center;
  list-style-type: none;
  text-decoration: none;
  color: #e5e5e5 !important;
}
nav ul li
{
  margin:40px;
}

a
{
  text-decoration: none;
  color: white;
}

.subtopic
{
  margin:10px 10px 30px 10px !important;
}
p{
  font-size: var(--fontsize-p);
}
h1,h2,h3,h4,p{
  margin: 10px;
}
.social
{
  position: absolute;
  margin-top: 20px;
  display: -ms-flexbox;
  display: flex;
  bottom: 60px;
  color: #e5e5e5 !important;
}
.social i
{
  padding-right: 30px;
  font-size: 25px;
  color: #14213d;
}
.social i:hover
{
  color: #ffffff;
}
.widecard
{
  border:0px solid #9a9b9c;
  display: -ms-flexbox;
  display: flex;
  margin:30px 0px 30px 0px;
}
.widecard:hover
{
  color: #1b1d20;
  background-color: #ffffff;
  -webkit-box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
          box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.active
{
  font-weight: bolder;
}
.skills ul li
{
  list-style-type:none;
  margin:30px;
}

@media all and (max-width: 450px) {
  :root {
    --fontsize-nav:1rem;
    --sidebar-flex:0;
    --typing-font:2rem;
  }
  .App
  {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .sidebar
  {
    padding: 30px;
    background: rgb(238,103,143);
    background: -webkit-gradient(linear, left top, right top, color-stop(50%, rgba(238,103,143,1)), color-stop(80%, rgba(132,124,252,1)));
    background: -webkit-linear-gradient(left, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
    background: -o-linear-gradient(left, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
    background: linear-gradient(90deg, rgba(238,103,143,1) 50%, rgba(132,124,252,1) 80%);
  }
  .condiv
  {
    padding: 50px ;
  }
  nav ul
  {
    display: -ms-flexbox;
    display: flex;
  }
  nav ul li
  {
    margin: 10px;
  }
  .social
  {
    left:0;
    width: 100vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    bottom: 30px;
  }
  .social i
  {
    padding-right: 0px;
  }
}


